$white:#fff;
$main:#dc0032;
$primary:#c71937;
$hover:#c30b2a;
$p:#333;
$black:#000;
// class
%flex {
    display: flex;
    align-items: center;
}

%trans {
    transition: all .6s ease-in-out;
}

%dimen {
    width: 100%;
    height: 100%;
}

%center {
    position: absolute;
    top: 50%;
    left: 50%;
}

ul {
    margin: 0;
}

a:link,
a:focus,
button,
button:focus,
input {
    text-decoration: none;
    outline: none;
    border: none;
}

.form-control:focus {
    box-shadow: none;
    border-color: $hover;
}

.title {
    position: relative;
    margin-bottom: 70px;
    i {
        position: absolute;
        left: 0px;
        top: 50%;
        transform: translateY(-50%);
        z-index: -1;
        opacity: .3;
    }
    h2 {
        text-align: left;
        color: #c30b2a;
        font-size: 30px;
        font-family: 'Nexa-bold.otf';
        span {
            font-size: 30px;
            font-family: 'Nexa-light.otf';
            color: #353535;
        }
    }
}