    /* upper-bar */
    
    .upper-bar {
        height: 60px;
        background-color: $white;
        line-height: 60px;
        .contant {
            display: flex;
            margin: 0;
            .last {
                margin-left: 20px;
            }
            .first .fa {
                font-size: 14px;
                line-height: 14px;
                padding: 0px 5px;
            }
            span {
                font-size: 14px;
                padding: 0 10px;
                font-family: 'Nexa-light.otf';
            }
        }
        .fab {
            padding: 0px 5px;
            font-size: 20px;
            color: $p;
        }
        .active {
            color: $main;
        }
        svg {
            margin-left: 60px;
            vertical-align: initial;
        }
    }
    /* upper bar */
    
    // slider
    .slider {
        background-color: $main;
        height: 100%;
        clip-path: polygon(0 0, 100% 0, 100% 86%, 0 100%, 0% 25%);
        @include Lg {
            height: 120vh!important;
        }
        @include Sm {
            clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 0% 38%);
            height: 100%!important;
            min-height: 100vh;
        }
    }
    
    // navbar
    .nav-bar {
        position: relative;
        top: 40px;
        background-color: $primary;
        .containt {
            @extend %flex;
            justify-content: space-between;
            overflow: hidden;
            position: relative;
            @extend %flex;
            justify-content: space-between;
            overflow: hidden;
            position: relative;
            background-color: $primary;
            @include Sm {
                padding: 20px 0px;
            }
        }
        // list
        .slide-bar {
            @include Sm {
                position: fixed;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-color: $white;
                z-index: 9999;
                transform-origin: right;
                transform: scaleX(0);
                transition: all .5s ease-in-out;
                &.active {
                    transform: scaleX(1);
                }
                &.transform-origin {
                    transform-origin: left;
                }
            }
        }
        nav {
            overflow: hidden;
            ul {
                @extend %flex;
                @extend %trans;
                &.list-unstyled.nav-item.open-nav-item {
                    transform: translateX(-850px);
                }
                // medium
                @include Sm {
                    display: block;
                    width: 300px;
                    margin: auto;
                    margin-top: 100px;
                    transform: translateX(0);
                }
                li {
                    padding: 22px 10px;
                    &.search {
                        padding-left: 60px;
                    }
                    &.active,
                    &:hover {
                        background-color: $hover;
                    }
                    @include Sm {
                        &.active,
                        &:hover {
                            background-color: transparent;
                        }
                    }
                    @include Sm {
                        margin-left: 0px;
                        padding: 12px 0px;
                        text-align: center;
                    }
                    a {
                        color: $white;
                        font-size: 18px;
                        font-family: 'Nexa-light.otf';
                        display: block;
                        @include Lg {
                            font-size: 15px;
                        }
                        @include Sm {
                            color: $black;
                            font-weight: bold;
                            transition: .5s;
                            font-size: 18px;
                        }
                        &:hover {
                            opacity: .6;
                        }
                    }
                }
                &:hover li.active,
                li:nth-of-type(7),
                li:nth-of-type(8) {
                    background-color: transparent;
                }
                @include Lg {
                    li:nth-of-type(7) {
                        display: none;
                    }
                }
            }
        }
        // search-col-sm
        .close-nav,
        div#close-btn,
        div#close-search {
            display: none;
            @include Sm {
                font-size: 18px;
                color: $p;
                text-align: center;
                cursor: pointer;
                display: block;
            }
        }
        .search-col {
            display: none;
            @include Sm {
                position: absolute;
                top: 0;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0px 30px;
                height: 80px;
            }
            .close-nav {
                display: none;
                @include Sm {
                    display: block;
                }
            }
        }
        // list
        // form lg
        form {
            position: absolute;
            right: -100%;
            top: 20px;
            @extend %trans;
            &.all {
                right: 0px;
            }
            // form sm
            @include Sm {
                position: fixed;
                top: 0px;
                left: 0%;
                right: auto;
                width: 100%;
                height: 100%;
                transform: translate(0);
                transition: all .9s ease-in-out;
                background-color: rgba(0, 0, 0, .8);
                z-index: 99999999;
                opacity: 0;
                visibility: hidden;
            }
            @include Sm {
                &.show-close {
                    opacity: 1;
                    visibility: visible;
                }
            }
        }
        .form-large {
            display: none;
            &.all {
                display: block;
                width: 200px;
            }
            input[type=search].one#search-input {
                width: 200px;
                position: absolute;
                height: 30px;
                transform: translateX(600px);
                transform-origin: left;
                right: 30px;
                top: 0px;
                background: transparent;
                overflow: hidden;
                color: $white;
                border-bottom: 1px solid #ffff;
                @extend %trans;
                &::placeholder {
                    color: $white;
                    font-size: 20px;
                    font-family: 'Nexa-light.otf';
                }
                &.open-search {
                    transform: translateX(0px);
                }
            }
            .close-large {
                background-color: transparent;
                background-color: transparent;
                cursor: pointer;
                display: none;
                background: transparent;
                transform-origin: left;
                position: absolute;
                right: 20px;
                @include Sm {
                    display: block;
                    transform: translate(-50%, -50%);
                }
                &.open-close {
                    display: block;
                }
                i {
                    color: $white;
                }
            }
        }
    }
    
    // .form-large
    // .form-sm
    .form-sm {
        display: none;
        @include Sm {
            display: block;
            position: absolute;
            left: 50%;
            top: 40%;
            width: 300px;
            transform: translate(-50%, -40%);
        }
        .input-sm {
            display: none;
            @include Sm {
                display: flex;
                position: relative;
                color: $white;
                margin-top: 100px;
            }
            #search-input-sm {
                display: none;
                @include Sm {
                    position: absolute;
                    display: block;
                    color: $white;
                    background-color: transparent;
                    width: 100%;
                    border-bottom: 1px solid $white;
                }
                &::placeholder {
                    color: $white;
                    font-size: 20px;
                    font-family: 'Nexa-light.otf';
                }
            }
        }
        .close-sm {
            background-color: transparent;
            position: absolute;
            right: 0;
            color: #fff;
        }
        div#close-btn {
            color: #fff;
        }
    }
    
    // // toggle
    .toggle {
        display: none;
        @include Sm {
            display: block;
            width: 30px;
            height: 30px;
            position: relative;
            right: 2%;
            font-size: 20px;
            color: $white;
            cursor: pointer;
        }
    }
    
    // navbar
    // slider
    .contant {
        position: relative;
        @extend %flex;
        justify-content: space-between;
        margin-top: 50px;
        @include Sm {
            top: 30px;
            margin: 0;
            flex-direction: column!important;
            text-align: center;
        }
        .title {
            position: relative;
            width: 75%;
            margin-bottom: 0;
            color: $white;
            @include Lg {
                width: 70%;
            }
            @include Sm {
                width: 100%;
                padding-bottom: 50px;
            }
            .slide {
                display: none;
                @include Sm {
                    display: block;
                    width: 100%;
                }
            }
            h1 {
                font-size: 50px;
                font-family: 'Nexa-bold.otf';
                @include Sm {
                    margin-top: 50px;
                }
                @include mob576 {
                    font-size: 30px;
                }
            }
            h5 {
                font-size: 50px;
                font-family: 'Nexa-light.otf';
                @include mob576 {
                    font-size: 30px;
                }
            }
            p {
                font-size: 20px;
                line-height: 1.7;
                font-family: 'Nexa-light.otf';
                margin-bottom: 50px;
                @include Lg {
                    font-size: 15px;
                }
                @include Sm {
                    font-size: 18px;
                    margin: 20px 0 0;
                }
                @include mob576 {
                    font-size: 15px;
                }
            }
            a {
                position: relative;
                border-radius: 4px;
                padding: 10px 17px;
                font-family: 'Nexa-light.otf';
                font-size: 18px;
                color: #bd0b27;
                background-color: $white;
                transform: perspective(500px);
                display: inline-block;
                @extend %trans;
                @include Sm {
                    margin-top: 20px;
                }
                &::before {
                    content: "";
                    position: absolute;
                    z-index: -1;
                    top: 0;
                    bottom: 0;
                    right: 0;
                    left: 0;
                    background: $primary;
                    transform: scaleX(0);
                    transform-origin: 50%;
                    border-radius: 4px;
                    @extend %trans;
                }
                &:hover::before {
                    transform: scaleX(1);
                }
                &:hover {
                    color: $white;
                }
            }
        }
    }
    
    .slide {
        width: 25%;
        .imgbox {
            position: relative;
            width: 100%;
            left: 0%;
            top: 20px;
            @extend %flex;
            justify-content: flex-end;
            @include Sm {
                justify-content: center;
            }
            .box {
                position: relative;
                height: 400px;
                width: 229px;
                @include Sm {
                    height: 360px;
                    width: 300px;
                }
                svg {
                    position: absolute;
                    bottom: 75px;
                    width: 229px;
                    height: 232px;
                    z-index: 228;
                    @include Sm {
                        position: relative;
                        top: 112px;
                        left: 50%;
                        transform: translateX(-50%);
                    }
                }
                .img {
                    background: url(../images/Layer-3.png) no-repeat center;
                    position: absolute;
                    width: 205px;
                    height: 325px;
                    z-index: 230;
                    right: 10px;
                    top: 0;
                    @include Sm {
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
                .par {
                    width: 100px;
                    height: 110px;
                    position: absolute;
                    left: -8%;
                    top: 8%;
                    overflow: hidden;
                    @include Sm {
                        left: 15%;
                    }
                    svg {
                        position: relative;
                        bottom: 0px;
                        top: 0px;
                        left: auto;
                        @include Sm {
                            bottom: 18px!important;
                            right: 0;
                            left: 50%;
                            width: auto;
                            height: auto;
                        }
                    }
                    p {
                        width: 75px;
                        text-align: center;
                        border-radius: 50%;
                        margin: 0;
                        @extend %center;
                        transform: translate(-50%, -50%);
                        font-size: 11.33px;
                        font-family: "Nexa Light";
                        color: rgb(39, 39, 39);
                        z-index: 999;
                    }
                }
            }
        }
    }
    
    //- header
    //- layout
    .lay-out {
        padding: 50px 0px 100px;
        text-align: center;
        @include Sm {
            padding: 50px 0px;
        }
        .contant {
            @extend %flex;
            justify-content: center;
            @include mob576 {
                flex-direction: column;
            }
            .cont {
                width: 60%;
                margin: auto;
                @include Xs {
                    width: 100%;
                }
                h2 {
                    font-size: 50px;
                    color: $hover;
                    font-family: 'Nexa-light.otf';
                    text-transform: uppercase;
                    padding-top: 30px;
                    margin-bottom: 50px;
                    @include Lg {
                        font-size: 45px;
                        margin-bottom: 0;
                    }
                    @include Sm {
                        font-size: 35px;
                        margin-bottom: 20px;
                        padding: 0;
                    }
                    @include mob576 {
                        font-size: 30px;
                        padding: 0;
                        margin-bottom: 30px;
                    }
                }
            }
        }
        a {
            text-transform: uppercase;
            font-family: 'Nexa-bold.otf';
            font-size: 18px;
            color: #353535;
            padding: 10px 18px;
            background-color: $hover;
            box-shadow: 0px 2px 10px;
            position: relative;
            display: inline-block;
            transform: perspective(1px) translateZ(0);
            box-shadow: 5px 5px 20px 3px $hover;
            @extend %trans;
            @include Sm {
                margin-top: 100px;
            }
            &::before {
                content: "";
                position: absolute;
                z-index: -1;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                background: $white;
                transform: scaleY(1);
                transform-origin: 50%;
                @extend %trans;
            }
            &:hover {
                color: $white;
            }
            &:hover::before {
                transform: scaleX(0);
            }
        }
        .img {
            width: 200px;
            height: 330px;
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
    
    //layout
    // .best-article
    .best-article {
        text-align: center;
        padding: 70px 0px;
        @include Sm {
            padding: 50px 0;
        }
        @include mob576 {
            padding: 25px 0px;
        }
        .owl-nav {
            position: absolute;
            width: 100%;
            margin: 0px;
            top: 50%;
            @include Sm {
                display: none;
            }
            button.owl-prev,
            button.owl-next {
                position: absolute;
                font-size: 40px;
                outline: none;
                background: transparent;
                border: none;
                color: #cd0b2b;
            }
            button.owl-prev,
            button.owl-next {
                position: absolute;
                font-size: 40px;
                outline: none;
                background: transparent;
                border: none;
                color: #cd0b2b;
            }
            button.owl-prev {
                left: -25px;
            }
            button.owl-next {
                right: -25px;
            }
            button.owl-prev:focus,
            button.owl-next:focus {
                outline: none;
                background: transparent;
                border: none;
                color: #cd0b2b;
            }
        }
        .owl-dots {
            display: none;
            @include Sm {
                display: block;
                margin-top: 30px;
            }
            span {
                width: 12px;
                height: 12px;
                display: block;
                border-radius: 50%;
                margin-left: 10px;
                background-color: #ccc;
            }
        }
        .card {
            height: 350px;
            overflow: hidden;
            @include mob576 {
                width: 70%;
                margin: auto;
            }
            @include mob425 {
                width: 90%;
            }
            .img-box {
                @extend %dimen;
                z-index: 2;
                background-color: #bd0b27;
                transition: all .5s ease-in-out;
                opacity: 1;
            }
            &:hover .img-box {
                transform: translateY(-90px);
            }
            img {
                @extend %dimen;
            }
            &:hover img {
                background-image: -moz-radial-gradient( 50% 50%, circle closest-side, rgb(151, 151, 150) 0%, rgb(195, 11, 42) 0%, rgb(173, 12, 39) 100%);
                background-image: -webkit-radial-gradient( 50% 50%, circle closest-side, rgb(151, 151, 150) 0%, rgb(195, 11, 42) 0%, rgb(173, 12, 39) 100%);
                background-image: -ms-radial-gradient( 50% 50%, circle closest-side, rgb(151, 151, 150) 0%, rgb(195, 11, 42) 0%, rgb(173, 12, 39) 100%);
                opacity: 0.549;
            }
            .detail {
                position: absolute;
                bottom: 0px;
                z-index: 1;
                width: 100%;
                height: 90px;
                background-color: #fff;
                p {
                    font-size: 16px;
                    color: #353535;
                    font-family: 'Nexa-light.otf';
                    padding: 20px 10PX;
                    background: $white;
                    margin: 0px;
                    text-align: left;
                    @include Xs {
                        font-size: 14px;
                    }
                }
            }
            &:hover li {
                transform: translateY(0PX);
                opacity: 1;
            }
            &:hover .list button {
                transform: translateY(0PX);
                opacity: 1;
            }
        }
        .btn {
            margin-top: 80px;
            border: 1px solid #c30b2a;
            padding: 12px 10px;
            transform: perspective(1px) translateZ(0);
            transition: all .5s ease-in-out;
            &.btn:hover {
                background-color: #c71937;
            }
            @include Xs {
                margin-top: 40px;
            }
            a {
                font-family: 'Nexa-light.otf';
                font-size: 18px;
                color: #c30b2a;
                margin-right: 20px;
            }
            .fas {
                color: #c30b2a;
            }
            &:hover a,
            &:hover .fas {
                color: $white;
            }
        }
        .list {
            @extend %center;
            display: block;
            margin: 0;
            transform: translate(-50%, -50%);
            text-align: center;
            z-index: 3;
            ul {
                display: flex;
                z-index: 9999;
                margin-bottom: 20px;
                li {
                    position: relative;
                    padding: 0px 10px;
                    transform: translateY(-100PX);
                    @extend %trans;
                    opacity: 0;
                    &:hover .fab {
                        transform: rotateY(360deg);
                    }
                    &:hover svg path {
                        fill: $white;
                    }
                    &:hover .fab {
                        color: #c30b2a!important;
                    }
                    &:nth-of-type(1) {
                        transition-delay: .1s;
                    }
                    &:nth-of-type(2) {
                        transition-delay: .2s;
                    }
                    &:nth-of-type(3) {
                        transition-delay: .3s;
                    }
                    &:nth-of-type(4) {
                        transition-delay: .4s;
                    }
                    .fab {
                        @extend %trans;
                    }
                }
                a {
                    @extend %center;
                    transform: translate(-50%, -50%);
                    color: #fff;
                    transform: translate(-50%, -50%);
                }
            }
            button {
                background-color: #cd0b2b;
                border-radius: 6px;
                width: 200px;
                height: 50px;
                transform: translateY(100PX);
                opacity: 0;
                position: relative;
                @extend %trans;
                &.owl-dot {
                    margin-top: 30px;
                }
                a {
                    font-size: 18px;
                    color: #c30b2a;
                    font-family: 'Nexa-light.otf';
                }
                &::before {
                    content: "";
                    position: absolute;
                    z-index: -1;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background: $white;
                    transform: scaleY(1);
                    transform-origin: 50%;
                    border-radius: 6px;
                    @extend %trans;
                }
                &:hover a {
                    color: $white;
                }
                &:hover::before {
                    transform: scaleX(0);
                }
            }
        }
    }
    
    .best-article .owl-theme .owl-dots .owl-dot.active span {
        background-color: #c71937;
    }
    
